import React from 'react';
import { Utils } from 'src/utils';
import { TextProps } from './text.types';
import './text.scss';

export const Text: React.FC<TextProps> = ({ children, inline, size }) => {

    const classes = Utils.classes({
        'text': true,
        'text--inline': inline,
        [`text--size-${size}`]: !!size
    });

    return (
        <div className={classes}>
            {children}
        </div>
    )
}