import React from 'react';
import { HpGrid, HpGridItem } from '@htmlplus/react';
import { Utils } from 'src/utils';
import { Code, Markup, Text } from 'src/components';
import { ParametersProps } from './parameters.types';
import './parameters.scss';

export const Parameters: React.FC<ParametersProps> = ({ items = [] }) => {

    const hasDefault = !!items.filter((item) => typeof item.default !== 'undefined').length;

    const hasType = !!items.filter((item) => typeof item.type !== 'undefined').length;

    const hasValue = !!items.filter((item) => typeof item.value !== 'undefined').length;

    const classes = Utils.classes({
        'parameters': true
    });

    return (
        <div className={classes}>
            {items.map((item, index, items) => (
                <HpGrid
                    key={item.name}
                    className={Utils.classes('last' && items.length - 1 === index)}
                >
                    <HpGridItem xs="12" sm="6" lg="3">
                        <Text>Name</Text>
                        <Text>{item.name}</Text>
                    </HpGridItem>
                    <HpGridItem xs="12" sm="6" lg="grow">
                        {hasType && <Text>Type</Text>}
                        {hasType && <Text>{item.type}</Text>}
                    </HpGridItem>
                    <HpGridItem class="default" xs="12" lg="auto">
                        {hasDefault && <Text>Default</Text>}
                        {hasDefault && <Text>{item.default}</Text>}
                    </HpGridItem>
                    <HpGridItem xs="12">
                        <Text>Description</Text>
                        <Markup>{item.description}</Markup>
                    </HpGridItem>
                    {hasValue && (
                        <HpGridItem xs="12">
                            <Text>Value</Text>
                            <Code language="js">{item.value}</Code>
                        </HpGridItem>
                    )}
                </HpGrid>
            ))}
        </div>
    )
}