import React from 'react';
import { HpGrid, HpGridItem } from '@htmlplus/react';
import { Section, Sidebar } from 'src/components';
import { components } from 'src/data';
import { Router } from 'src/services';
import { Utils } from 'src/utils';
import './default.scss';

export const LayoutDefault: React.FC = ({ children }) => {

    const router = Router.useRouter();

    const items = [
        {
            title: 'UI Components',
            items:
                components
                    .filter((item) => item.parent)
                    .map((component) => ({
                        // @ts-ignore
                        title: component.title + (component.development ? ' (development)' : ''),
                        path: Router.toPath('ROUTE:COMPONENT:DETAILS', { key: component.key })
                    }))
        },
        {
            title: 'Framework Integrations',
            items: [
                {
                    title: 'Overview',
                    path: Router.toPath('ROUTE:FRAMEWORK:OVERVIEW')
                },
                {
                    title: 'React',
                    path: Router.toPath('ROUTE:FRAMEWORK:REACT')
                },
                {
                    title: 'Angular',
                    path: Router.toPath('ROUTE:FRAMEWORK:ANGULAR')
                },
                {
                    title: 'Vue',
                    path: Router.toPath('ROUTE:FRAMEWORK:VUE')
                },
                {
                    title: 'JavaScript',
                    path: Router.toPath('ROUTE:FRAMEWORK:JAVASCRIPT')
                }
            ]
        }
    ]

    const all: Array<any> = items
        .map((item) => {

            if (!item.items) return null;

            return item.items.map((subItem) => ({ ...subItem, category: item.title }));
        })
        .flat();

    const index = all.findIndex((item) => item.path === router.match.url);

    const next = all[index + 1]

    const prev = all[index - 1];

    const classes = Utils.classes({
        'layout-default': true
    });

    return (
        <div className={classes}>
            <HpGrid wrap="off">
                <HpGridItem xs="auto">
                    <Sidebar items={items} />
                </HpGridItem>
                <HpGridItem xs="grow">
                    {/* TODO: use box */}
                    <div className="layout-default__content">
                        {children}
                        <Section size="page">
                            <HpGrid justify="between">
                                <HpGridItem xs="auto">
                                    {!!prev && (
                                        <Router.Link to={prev.path}>
                                            PREV
                                            <br />
                                            {prev.title}
                                            <br />
                                            {prev.category}
                                        </Router.Link>
                                    )}
                                </HpGridItem>
                                <HpGridItem xs="auto">
                                    {!!next && (
                                        <Router.Link to={next.path}>
                                            NEXT
                                            <br />
                                            {next.title}
                                            <br />
                                            {next.category}
                                        </Router.Link>
                                    )}
                                </HpGridItem>
                            </HpGrid>
                        </Section>
                    </div>
                </HpGridItem>
            </HpGrid>
        </div>
    )
}
