import React from 'react';
import { Markup } from 'src/services';
import { Alert, Text } from 'src/components';

const mapper: any = {
    p: {
        name: Text,
        props: {
            size: 'body'
        }
    },
    h1: {
        name: Text,
        props: {
            size: 'header'
        }
    },
    blockquote: {
        name: Alert,
        props: {
            type: (element: HTMLElement) => {

                const text = (element.textContent || '').trim();

                const types = ['[ERROR]', '[INFO]', '[SUCCESS]', '[WARNING]'];

                for (let i = 0; i < types.length; i++) {

                    const type = types[i];

                    if (text.indexOf(type) === -1) continue;

                    element.innerHTML = element.innerHTML.replace(type, '');

                    return type.replace(/\[|\]/g, '').toLowerCase();

                }
            }
        }
    }
}

const convert = (input: Element) => {

    const map = mapper[input.localName] || {};

    const name = map.name || input.localName;

    const props = map.props || {};

    props.key = Math.random();

    const children: Array<React.ReactNode> = [];

    Object.keys(props)
        .forEach((key) => {

            if (typeof props[key] !== 'function') return null;

            props[key] = props[key](input);
        });

    Array.from(input.children)
        .forEach((child) => {

            children.push(convert(child));
        })

    return React.createElement(name, props, children.length ? children : input.textContent);
}

export const parser = (input: any) => {

    const value = Markup.parse((input || '').toString(), { headerIds: false });

    const wapper = document.createElement('div');

    wapper.innerHTML = value;

    return convert(wapper);
}

