import React from 'react';
import { Utils } from 'src/utils';
import { AlertProps } from './alert.types';
import './alert.scss';

export const Alert: React.FC<AlertProps> = ({ children, type }) => {

    const classes = Utils.classes({
        'alert': true,
        [`alert--${type}`]: !!type
    });

    return (
        <div className={classes}>
            {children}
        </div>
    )
}