import React from 'react';
import { HpTabs, HpTabsBar, HpTabsTab, HpTabsPanels, HpTabsPanel } from '@htmlplus/react';
import { Utils } from 'src/utils';
import { Code } from 'src/components';
import { ExampleProps, Languages } from './example.types';
import './example.scss';

// TODO
import * as core from './core';

export const Example: React.FC<ExampleProps> = ({ value }) => {

    const code = value.code;

    const Preview = value.tag as any;

    // TODO
    !customElements.get(Preview) && value.preview(core);

    const tabs = Object.keys(code)
        .map((key) => ({
            key,
            title: Utils.capitalize(key),
            language: Languages[key as keyof typeof Languages],
            value: code[key]
        }));

    const classes = Utils.classes({
        'example': true
    });

    return (
        <div className={classes}>
            <HpTabs value="preview">
                <HpTabsBar>
                    <HpTabsTab value="preview">
                        Preview
                    </HpTabsTab>
                    {tabs.map((item: any) => (
                        <HpTabsTab
                            key={item.key}
                            disabled={!item.value}
                            value={item.key}
                        >
                            {item.title}
                        </HpTabsTab>
                    ))}
                </HpTabsBar>
                <HpTabsPanels>
                    <HpTabsPanel value="preview">
                        <Preview />
                    </HpTabsPanel>
                    {tabs.map((item: any) => (
                        <HpTabsPanel key={item.key} value={item.key}>
                            <Code language={item.language}>
                                {item.value}
                            </Code>
                        </HpTabsPanel>
                    ))}
                </HpTabsPanels>
            </HpTabs>
        </div>
    )
}