import React from 'react';
import { Utils } from 'src/utils';
import { parser } from './markup.parser';
import { MarkupProps } from './markup.types';
import './markup.scss';

export const Markup: React.FC<MarkupProps> = ({ children }) => {

    const classes = Utils.classes({
        'markup': true
    });

    return (
        <div className={classes}>
            {parser(children)}
        </div>
    )
}