import React, { useEffect } from 'react';
import { Utils } from 'src/utils';
import './framework-selector.scss';

export const FrameworkSelector: React.FC<any> = ({ onChange }) => {

    const frameworks = [
        {
            key: 'react',
            value: 'React'
        },
        {
            key: 'vue',
            value: 'Vue'
        },
        // {
        //     key: 'angular',
        //     value: 'Angular'
        // },
        {
            key: 'javascript',
            value: 'Javascript'
        },
    ];

    const params = window.location.search
        .slice(1)
        .split('&')
        .map((text) => {

            const sections = text.split('=');

            return { [sections[0]]: sections[1] }
        })
        .reduce((result, item) => ({ ...result, ...item }), {});

    const value = (params.fr || 'react').toLowerCase();

    useEffect(
        () => onChange({ target: { value } }),
        [value, onChange]
    );

    const change = (event: any) => {

        const url = new URL(window.location.href);

        url.searchParams.set('fr', event.target.value);

        url.search = url.searchParams.toString();

        // TODO
        window.history.pushState({}, '', url.toString());

        onChange(event);
    }

    const classes = Utils.classes({
        'framework-selector': true
    });

    return (
        <div className={classes}>
            <select value={value} onChange={change}>
                {frameworks.map((item) => (
                    <option key={item.key} value={item.key}>
                        {item.value}
                    </option>
                ))}
            </select>
        </div>
    )
}